import React from 'react';
import PropTypes from 'prop-types';



export default class Filter extends React.Component {

  constructor(props, ...args) {
    super(props, ...args);

    this.state = {
      filteredItems: _filter(props)
    };
  }



  componentWillReceiveProps() {
    this.setState((prevProps, props) => { // eslint-disable-line consistent-return
      if (props.items !== prevProps.items || props.predicate !== prevProps.predicate || props.predicateArg !== prevProps.predicateArg) {
        return {
          filteredItems: _filter(props)
        };
      }
    });
  }


  render() {
    return this.props.render(this.state.filteredItems);
  }
}


Filter.propTypes = {
  items: PropTypes.array,

  render: PropTypes.func.isRequired,

  predicate:    PropTypes.func.isRequired,
  predicateArg: PropTypes.any // eslint-disable-line react/forbid-prop-types
};



/* eslint-disable valid-jsdoc */



// region Private Helpers

/** @private */
function _filter({ items, predicate, predicateArg }) {
  if (items == null) {
    return items;
  }

  const ret = [];

  for (let i = 0; i < items.length; i++) {
    const item = items[i];

    if (predicate(item, i, predicateArg)) {
      ret.push(item);
    }
  }

  return ret;
}

// endregion
